.myModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
}

.myModalContent {
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 250px;
    transform: translateY(-1000%);
    transition: all 0.3s ease;
}

.myModalContent.active {
    transform: translateY(0%);
}
.myModal.active {
    opacity: 1;
    visibility: visible;
}
