* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;

    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}

.post {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid teal;
    padding: 15px;
    margin: 15px 0;
}

.post__btns {
    padding: 0 0 0 10px;
    display: flex;
    gap: 10px;
}

/* animation for posts */

.post-enter {
    transform: translateX(-350px);
}
.post-enter-active {
    transform: translateX(0px);

    transition: all 500ms ease-in;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    transform: translateX(-350px);
    transition: all 500ms ease-in;
}

/* pagination */
.pagination__wrapper {
    margin-top: 30px;
    display: flex;
}

.pagination {
    width: 40px;
    height: 40px;
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination.active {
    background-color: teal;
    color: #fff;
}

/* nav */
.navbar {
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    background: teal;
}

.navbar__links {
    display: flex;
    gap: 10px;
}

.navbar__links a {
    color: white;
    text-decoration: none;
}
